<template>
  <div class="enquirylist">
    <p>显示页面</p>
    <ul class="contentlist">
      <li>
        <div class="table2-title table2">
          <p>{{ zip }}</p>
          <p>{{ country }}</p>
          <p>{{ city }}</p>
          <p>{{ price }}</p>
          <p>{{ type }}</p>
          <p>{{ imports }}</p>
          <p>{{ date }}</p>
        </div>
      </li>
      <li
        v-for="(Numbers, index) in ziplist.slice(
          (currentPage - 1) * pagesize,
          currentPage * pagesize
        )"
        :key="index"
      >
        <div class="table2">
          <a href="#" @click="toEnquiryDetail(index)">
            <p v-text="Numbers.zip"></p>
          </a>
          <p v-text="Numbers.country"></p>
          <p v-text="Numbers.city"></p>
          <p v-text="Numbers.total_prices"></p>
          <p v-text="Numbers.type_traffic"></p>
          <p v-text="Numbers.type_jinchukou"></p>
          <p v-text="Numbers.service_time"></p>
          <p v-text="Numbers.id" class="undisplay"></p>
        </div>
      </li>
    </ul>
    <el-pagination
      @current-change="handleCurrentChange"
      :page-size="pagesize"
      :pager-count="11"
      layout="prev, pager, next"
      :total="ziplist.length"
      :current-page.sync="currentPage"
    >
    </el-pagination>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "EnquiryList",
  data() {
    return {
      currentPage: 1,
      pagesize: 14,
      base_api_url: "https://baojia.widesight.cn/backend/public",
      zip: "邮编",
      country: "国家",
      city: "城市",
      price: "总价",
      type: "货物类型",
      imports: "进出口",
      date: "日期",
      ziplist: [],
      item: [],
    };
  },
  created() {
    this.getParams();
  },
  methods: {
    getParams () {
        // 取到路由带过来的参数 
        this.routerParams = this.$route.query.list
        // 将数据放在当前组件的数据内
      //  console.log("aaa",this.routerParams)
      },
    toEnquiryDetail(list_id) {
      // console.log(list_id)
      this.msg = "";
      let id = this.ziplist[list_id]["id"];
      let transportType = this.ziplist[list_id]["type_traffic"];
      //this.$refs[list_id].validate((valid) => { //表单验证通过 同步方法
      // console.log(id)
      try {
        axios
          .get(this.base_api_url + "/api/detailedquery", {
            // country: this.searchForm.country, // 国家
            // city: this.searchForm.city, // 城市
            // zip: this.searchForm.zip, //邮编
            // type_jinchukou: this.searchForm.importOrOutport, //进口出口
            // box_pile: this.searchForm.box, //海运箱型
            // time: this.searchForm.daterange, //查询日期范围
            params: {
              type_traffic: transportType, //运输类型
              id: id, //上传id
            },
          })
          .then((res) => {
           console.log(res.data[0],"11");
            this.$router.push({
              name: "enquirydetail",
              params: {
                data: res.data[0],
                item: res.data.item,
              },
            });
          });
      } catch (e) {
        console.log(e); //捕捉 axios 请求错误
      }
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
      // console.log(this.currentPage,";;;") //点击第几页
    },
  },
  mounted: function () {
    this.ziplist = this.$route.query.list;
  },
};
</script>

<style scoped>
.contentlist {
  min-height: 52rem;
  padding-inline-start: 0px;
  list-style: none;
  width: fit-content;
  border: #ddd 1px solid;
  text-align: center;
  padding: 2rem;
  padding-top: 1rem;
  border-radius: 10px;
}

.contentlist li {
  border-bottom: 1px #ddd solid;
}

.table2 {
  display: flex;
  flex-direction: row;
}

.table2 p {
  width: 200px;
}

.undisplay {
  display: none;
}
</style>



